.post{
    background-color: white ;
    max-width: 500px;
    border: 2px solid lightgray;
    margin-bottom: 45px;
}
.post_img
{
    width: 100%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;

}
.post_text{
    font-weight: normal;
    padding: 20px;
}
.post_header
{
    display: flex;
    align-items: center;
    padding: 20px 10px;
}
.post_avatar{
    margin-right: 10px;
}